header {
	width: fit-content;
  color: #BF91F3;
}


.c-primary {
  color: #0969da;
}
.bg-primary {
  color: #0969da;
}

.border-color-primary {
  border-color: #0969da !important;
}

.form-select:focus {
  border-color: #0969da;
  box-shadow: 0 0 0 0.25rem rgb(9, 105, 218, 0.25);
}

.c-sub-title {
  color: '#333';
}

button:disabled {
  cursor: not-allowed;
}

.green-class {
  color: #2caeba;
  background-color: #e0fcff;
}

.pink-class {
  color: #da4a91;
  background-color: #ffe0f0;
}

.purple-class {
  color: #5d55fa;
  background-color: #e6e6ff;
}

.yellow-class {
  color: #f0b429;
  background-color: #fffbea;
}

.txt-muted {
  color: rgb(87, 96, 106);
}

.txt-title {
  color: rgb(36, 41, 47);
}


.card-container .card{
  transition: all 300ms;
  border: 2px solid #e1e1e1;
}

.card-container:hover .card
{
  border-color: #6FA4FC;
  transform: translateY(-2px);
}


::-webkit-scrollbar {
	width: 9px; /* Mostly for vertical scrollbars */
	height: 9px; /* Mostly for horizontal scrollbars */
}

::-webkit-scrollbar-track {
	/* Foreground */
  padding: 10px;
	background-color: #e1e1e1;
	box-shadow: 0 0  3px #999 inset;
}

::-webkit-scrollbar-thumb {
	/* Background */
	box-shadow: 0 0 3px 1px #666  inset;
	background-color: #888;
	/* border-radius: 3px; */
	transition: all .5s;
}

::-webkit-scrollbar-thumb:hover {
  box-shadow: 0 0 3px 1px #888  inset;
	background-color: #666;
}


